    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
}

.ghostclose {
    width: 80%;
    height: 100%;
    background: none;
    border: none;
    -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);
}

.menu-list {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 70%;
    position: relative;
    top: -110px;
}

.links_link {
    text-decoration: none;
    color: rgb(8, 0, 0);
    font-size: 24px;
    padding: 10px;
    margin-bottom: 40px;
    font-family: 'Montserrat', sans-serif;
    width: 100%;
    padding-left: 40px;
    color: white;
}

.links_link:hover {
    color: rgb(8, 0, 0);
    background-color: white;
}

.menu-list img {
    width: 80%;
    padding-left: 20px;
}

.menu-list button {
    margin-top: 20px;
    font-size: 80px;
    width: 100%;
    padding: 0;
    border: none;
    cursor: pointer;
    background: none;
    color: white;
}

@media (max-width: 720px) {
    .menu {
        width: 100%;
        height: 100vh;
    }
    .menu-inner {
        width: 50%;
    }
    .menu-list {
        position: relative;
        top: -50px;
    }
    .links_link {
        padding-left: 10px;
        font-size: 22px;
        margin-bottom: 50px;
    }
    .menu-list button {
        font-size: 60px;
        margin-bottom: 40px;
    }
    .ghostclose {
        width: 50%;
    }
}

@media (max-height: 700px) {
    .menu-list {
        top: 0px;
    }
    .links_link {
        font-size: 18px;
        margin-bottom: 40px;
    }
}
